// src/components/home/Products.js
'use client'

import React from 'react'
import Image from 'next/image'
import Link from 'next/link' // Link import qilindi
import { Star, ShoppingCart, Heart, Minus, Plus } from 'lucide-react'
import { useCart } from '@/context/CartContext'
import productsData from '@/datas/Products.json'

import { Button } from '@/components/ui/button'
import { Card, CardContent, CardFooter } from '@/components/ui/card'
import {
	Carousel,
	CarouselContent,
	CarouselItem,
	CarouselNext,
	CarouselPrevious,
} from '@/components/ui/carousel'

const Products = () => {
	const { getItemQuantity, increaseCartQuantity, decreaseCartQuantity } =
		useCart()

	const formatPrice = price => {
		return new Intl.NumberFormat('uz-UZ').format(price)
	}

	return (
		<section className='py-10 bg-white'>
			<div className='container mx-auto px-4'>
				<h2 className='text-2xl font-bold mb-6 text-gray-800'>
					Mashhur Mahsulotlar
				</h2>

				<div className='grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4'>
					{productsData.map(product => {
						const quantity = getItemQuantity(product.id)

						return (
							<Card
								key={product.id}
								className='group flex flex-col justify-between border-none shadow-sm hover:shadow-lg transition-shadow duration-300 rounded-xl overflow-hidden'
							>
								{/* --- Rasm Karuseli --- */}
								<div className='relative w-full aspect-[3/4] bg-gray-50'>
									{/* Chegirma */}
									{product.discount > 0 && (
										<div className='absolute top-2 left-2 z-10 bg-purple-600 text-white text-[10px] font-bold px-1.5 py-0.5 rounded'>
											-{product.discount}%
										</div>
									)}
									{/* Like tugmasi */}
									<button className='absolute top-2 right-2 z-10 text-gray-400 hover:text-red-500 transition'>
										<Heart className='w-5 h-5' />
									</button>

									<Carousel className='w-full h-full'>
										<CarouselContent>
											{product.images.map(img => (
												<CarouselItem
													key={img.id}
													className='relative w-full h-full'
												>
													{/* LINK: Rasmga bosganda mahsulot sahifasiga o'tadi */}
													<Link
														href={`/product/${product.id}`}
														className='block w-full h-full cursor-pointer'
													>
														<div className='relative w-full h-[220px]'>
															<Image
																src={img.imageUrl}
																alt={product.nomi}
																fill
																className='object-contain p-2 mix-blend-multiply'
															/>
														</div>
													</Link>
												</CarouselItem>
											))}
										</CarouselContent>
										<div className='opacity-0 group-hover:opacity-100 transition-opacity'>
											<CarouselPrevious className='left-1 h-6 w-6 z-20' />
											<CarouselNext className='right-1 h-6 w-6 z-20' />
										</div>
									</Carousel>
								</div>

								{/* --- Ma'lumotlar --- */}
								<CardContent className='p-3 flex-grow flex flex-col gap-1'>
									{/* LINK: Nomiga bosganda ham o'tadi */}
									<Link
										href={`/product/${product.id}`}
										className='hover:text-blue-600 transition-colors'
									>
										<h3
											className='text-sm text-gray-700 line-clamp-2 leading-tight min-h-[40px]'
											title={product.nomi}
										>
											{product.nomi}
										</h3>
									</Link>

									<div className='flex items-center gap-1 mt-1'>
										<Star className='w-3 h-3 text-yellow-400 fill-yellow-400' />
										<span className='text-xs text-gray-500 font-medium'>
											{product.rating}
										</span>
										<span className='text-xs text-gray-400'>
											({product.reviewsCount} sharh)
										</span>
									</div>

									<div className='mt-2'>
										{product.oldPrice && (
											<div className='text-xs text-gray-400 line-through'>
												{formatPrice(product.oldPrice)} so'm
											</div>
										)}
										<div className='text-sm font-bold text-blue-900'>
											{formatPrice(product.newPrice)} so'm
										</div>
									</div>
								</CardContent>

								{/* --- Savatga Tugmalari (Link ta'sir qilmaydi) --- */}
								<CardFooter className='p-3 pt-0'>
									{quantity === 0 ? (
										<Button
											variant='outline'
											className='w-full border-orange-500 text-orange-500 hover:bg-orange-500 hover:text-white transition-colors h-9 text-xs font-semibold rounded-lg flex items-center justify-center gap-2'
											onClick={() => increaseCartQuantity(product.id)}
										>
											<ShoppingCart className='w-4 h-4' />
											Savatga
										</Button>
									) : (
										<div className='w-full flex items-center justify-between border border-orange-500 rounded-lg h-9 overflow-hidden'>
											<button
												onClick={() => decreaseCartQuantity(product.id)}
												className='w-10 h-full flex items-center justify-center bg-white text-gray-500 hover:bg-gray-100 active:bg-gray-200 transition'
											>
												<Minus className='w-4 h-4' />
											</button>
											<span className='flex-1 text-center text-sm font-bold text-gray-800'>
												{quantity}
											</span>
											<button
												onClick={() => increaseCartQuantity(product.id)}
												className='w-10 h-full flex items-center justify-center bg-white text-gray-500 hover:bg-gray-100 active:bg-gray-200 transition'
											>
												<Plus className='w-4 h-4' />
											</button>
										</div>
									)}
								</CardFooter>
							</Card>
						)
					})}
				</div>
			</div>
		</section>
	)
}

export default Products
