// src/components/home/SubNavbar.js
'use client'

import * as React from 'react'
import Image from 'next/image'
import Link from 'next/link'
import Autoplay from 'embla-carousel-autoplay'

import {
	Carousel,
	CarouselContent,
	CarouselItem,
	CarouselNext,
	CarouselPrevious,
} from '@/components/ui/carousel'

// JSON ma'lumotni import qilamiz
import bannerData from '@/datas/SubNavbar.json'

const SubNavbar = () => {
	// Autoplay plagini (har 4 soniyada aylanadi)
	const plugin = React.useRef(
		Autoplay({ delay: 4000, stopOnInteraction: true })
	)

	return (
		<div className='w-full bg-gray-50 py-4'>
			<div className='container mx-auto px-4'>
				<Carousel
					plugins={[plugin.current]}
					className='w-full'
					onMouseEnter={plugin.current.stop}
					onMouseLeave={plugin.current.reset}
				>
					<CarouselContent>
						{bannerData.map(banner => (
							<CarouselItem key={banner.id} className='pl-4'>
								<Link
									href={banner.urlLink}
									target='_blank'
									className='block relative w-full h-[200px] md:h-[350px] lg:h-[400px]'
								>
									{/* Rasm */}
									<div className='relative w-full h-full rounded-2xl overflow-hidden shadow-sm hover:shadow-md transition-shadow duration-300'>
										<Image
											src={banner.imageUrl}
											alt={`Banner ${banner.id}`}
											fill
											className='object-cover'
											priority={banner.id === 1} // Birinchi rasmni tezroq yuklaydi
										/>
									</div>
								</Link>
							</CarouselItem>
						))}
					</CarouselContent>

					{/* O'tkazish tugmalari (chap va o'ng) */}
					<CarouselPrevious className='absolute left-4 hidden md:flex' />
					<CarouselNext className='absolute right-4 hidden md:flex' />
				</Carousel>
			</div>
		</div>
	)
}

export default SubNavbar
